export default {
	data() {
		return {
			textFlag: false,
			initArray: ['北京市', '北京市', '东城区'],
			ChangeArray: [],
			index: 0,
			nowDate: this.$custom.getYMD()
		};
	},
	props: {
		mode: String,	// picker 的类型
		name: String,	// 调用的类型
		ClearBtnFlag: Boolean,	// 显隐清空按钮
		echo: String,	// 数据回显
	},
	watch: {
		echo(val) {
			// val => 北京市, 北京市, 朝阳区 <String>
			// 传入值, 用于回显
			this.textFlag = true
			this.ChangeArray = val.split(',')
		}
	},
	methods: {
		bindPickerChange(e) {
			this.textFlag = true
			this.ChangeArray = e.detail.value,
			this.changePicker()
		},
		clearCFDAddress() {
			// 切换隐藏内容
			this.textFlag = false
			this.ChangeArray = ""
			this.changePicker()
		},
		changePicker() {
			this.$emit('changePicker', {
				ChangeArray: this.ChangeArray, 
				name: this.name
			})
		}
	}
}
